* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #e1e863;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  display: block;
}

.search-form {
  font: inherit;
  background-color: rgba(17, 152, 220, .79);
  justify-content: center;
  margin-bottom: 25px;
  padding: 15px;
  display: flex;
}

.search-form input, .search-form button {
  border: none;
  border-radius: 10px;
  outline: none;
  margin: 10px;
  padding: 10px 20px;
}

.search-form button {
  background-color: rgba(255, 255, 252, .992);
}

.search-form button:hover {
  background-color: #ebef08;
}

.gallery {
  max-width: 90vw;
  grid-gap: 15px;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  grid-auto-rows: 300px;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
  display: grid;
}

.photo-card {
  margin-bottom: 50px;
  position: relative;
}

.photo-card img:hover {
  transform: scale(1.03);
}

.photo-card img {
  height: 100%;
  width: 100%;
  object-fit: cover;
}

.info {
  grid-template-columns: repeat(4, auto);
  grid-auto-rows: minmax(50px, auto);
  display: grid;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .2), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px -1px rgba(0, 0, 0, .12);
}

.info-item {
  text-align: center;
  margin: 0;
}

.load-more {
  border: none;
  border-radius: 10px;
  outline: none;
  margin-top: 50px;
  margin-left: auto;
  margin-right: auto;
  display: block;
}

